

























import Vue from 'vue'

export default Vue.extend({
    name: "CreateTodo",
    data() {
        return {
            nameTodo: ''
        }
    },
    methods: {
        submitCreate(): void {
            if (this.nameTodo.trim()) {
                const newTodo = {
                    idTodo: Date.now(),
                    title: this.nameTodo,
                    completed: false
                }
                this.$emit('add-todo', newTodo)
                this.nameTodo = ''
            }
        }
    },
})
