















import Vue from 'vue'
import TodoList from "@/components/TodoList.vue";

export default Vue.extend({
    name: "Home",
    components: {
        TodoList
    },
    props: {
        TodoLists: {
            type: Array
        },
        editMode: Boolean
    },
    methods: {
        changeEditMode(idList: number) {
            this.$emit('change-edit-mode', idList)
        },
        removeTodoList(idList: number): void {
            this.$emit('remove-todo-list', idList)
        }
    },
})
