

























import Vue from 'vue'

export default Vue.extend({
    name: "AddTodoList",
    data() {
        return {
            nameList: ''
        }
    },
    methods: {
        submitCreate(): void {
            if (this.nameList.trim()) {
                const newTodoList = {
                    idList: Date.now(),
                    nameList: this.nameList,
                    todos: []
                }
                this.$emit('add-todo-list', newTodoList)
                this.nameList = ''
            }
        }
    },
})
